import 'script-loader!../../lib/arjs/threex-arpatternfile.js';
import 'script-loader!../../lib/threejs/three.min.js';

import Property from '../nemo-ar/property';
import QrcodeGenerator from 'qrcode-generator';
import MarkerInfo from './marker-info';
import Utils from '../nemo-ar/utils';

export default class NemoArMarker {
    constructor (pattRatio = Property.PATTERN_RATIO) {
        this.__overriding();

        this.__markerInfo = new MarkerInfo();
        this.__pattRatio = pattRatio;
    }

    setPattRatio(ratio) {
        const me = this;
        me.__pattRatio = ratio;
    }

    makeFromFile(imgFile, qrText) {
        const me = this;

        me.__markerInfo = new MarkerInfo();

        return Utils.convertNonTransparentImg(imgFile).then(function (dataUrl) {
            me.__markerInfo.imageUrl = dataUrl;
            return me.__markerImage();

        }).then(function (dataUrl) {
            me.__markerInfo.markerImageUrl = dataUrl;
            return me.__qrImage(qrText);

        }).then(function (dataUrl) {
            me.__markerInfo.qrImageUrl = dataUrl;
            return me.__markerPattern();

        }).then(function (data) {
            me.__markerInfo.markerPattern = data;
            return Promise.resolve(me.__markerInfo);

        }).catch(function () {
            me.__markerInfo = new MarkerInfo();
            return Promise.resolve(me.__markerInfo);
        });
    }

    qrFromString(str) {
        const me = this;

        return me.__qrImage(str);
    }

    downloadFile(dataUrl) {
        THREEx.ArPatternFile.triggerDownload(dataUrl);
    }

    __qrImage(qrText) {
        return new Promise(function (resolve, reject) {
            try {
                if (qrText) {
                    const qrcode = QrcodeGenerator(0, 'H');

                    qrcode.addData(qrText);
                    qrcode.make();
                    resolve(qrcode.createDataURL(10, 10));

                } else {
                    resolve('');
                }

            } catch (error) {
                reject(error);
            }
        });
    }

    __markerImage() {
        const me = this;

        return new Promise(function (resolve, reject) {
            try {
                THREEx.ArPatternFile.buildFullMarker(me.__markerInfo.imageUrl, me.__pattRatio, 512, 'black', resolve);

            } catch (error) {
                console.error(error);
                reject(error);
            }
        });
    }

    __markerPattern() {
        const me = this;

        return new Promise(function (resolve, reject) {
            try {
                THREEx.ArPatternFile.encodeImageURL(me.__markerInfo.imageUrl, resolve);

            } catch (error) {
                console.error(error);
                reject(error);
            }
        });
    }

    __overriding() {
        // 캔버스에 이미지를 그릴 때 경계선에 의도하지 않은 페더가 생긴다. 이것을 없애기 위해 수치를 반올림해서 정수화시킨다.
        if (THREEx && THREEx.ArPatternFile.buildFullMarker) {
            console.log('Overriding THREEx.ArPatternFile.buildFullMarker');

            THREEx.ArPatternFile.buildFullMarker =  function(innerImageUrl, pattRatio, size, color, onComplete){
                const whiteMargin = 0.1;
                const blackMargin = (1 - 2 * whiteMargin) * ((1-pattRatio)/2);

                const innerMargin = whiteMargin + blackMargin;

                const canvas = document.createElement('canvas');
                const context = canvas.getContext('2d');
                canvas.width = canvas.height = size;

                context.fillStyle = 'white';
                context.fillRect(0,0,canvas.width, canvas.height);

                // copy image on canvas
                context.fillStyle = color;
                context.fillRect(
                    Math.round(whiteMargin * canvas.width),
                    Math.round(whiteMargin * canvas.height),
                    Math.round(canvas.width * (1-2*whiteMargin)),
                    Math.round(canvas.height * (1-2*whiteMargin))
                );

                // clear the area for innerImage (in case of transparent image)
                context.fillStyle = 'white';
                context.fillRect(
                    Math.round(innerMargin * canvas.width),
                    Math.round(innerMargin * canvas.height),
                    Math.round(canvas.width * (1-2*innerMargin)),
                    Math.round(canvas.height * (1-2*innerMargin))
                );


                // display innerImage in the middle
                const innerImage = document.createElement('img');
                innerImage.addEventListener('load', function(){
                    // draw innerImage
                    context.drawImage(innerImage,
                        Math.round(innerMargin * canvas.width),
                        Math.round(innerMargin * canvas.height),
                        Math.round(canvas.width * (1-2*innerMargin)),
                        Math.round(canvas.height * (1-2*innerMargin))
                    );

                    const imageUrl = canvas.toDataURL();
                    onComplete(imageUrl);
                });
                innerImage.src = innerImageUrl;
            };
        } else {
            const str = [
                'Overriding THREEx.ArPatternFile.buildFullMarker를 찾을 수 없습니다.',
                '해당 함수를 재정의 해야 이미지 테두리에 흰 선이 나오지 않습니다.'
            ].join('\n');

            console.warn(str);
        }
    }
}

// 전역에서 호출 가능하도록 추가
global.NemoArMarker = NemoArMarker;